@import '../../utils/fonts.scss';
.footer {
  display: block;

  &-content {
    border-top: 1px solid #e2e6e9;

    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 32px;
  }

  &-container {
    display: flex;
    flex-grow: 1;
    justify-content: space-evenly;
  }

  &-logo-cont {
    display: flex;
    flex-grow: 1;
  }

  &-logo {
    width: 89px;
    height: 32px;
    object-fit: contain;
    background-color: #fff;
  }

  &-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &-arrow {
    display: inline-flex;
    align-items: center;
    border: 1px solid #b4bdc3;
    background-color: #f0f0f000;
    cursor: pointer;

    &-img {
      max-width: 100%;
    }
  }

  &-back-to-top {
    margin-left: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;

    &-container {
      font-family: Mont;
      line-height: 32px;
      color: #89939a;
      font-weight: 600;
      display: flex;
      flex-grow: 1;
      justify-content: flex-end;

      &-topress {
        transition: color 0.3s ease;
        cursor: pointer;
        display: flex;
        &:hover {
          color: black;

          & .footer-arrow {
            border: 2px solid #b4bdc3 !important;
            margin-right: 0;
            margin-left: 15px;
            box-sizing: border-box;
          }
        }
        & button {
          margin-right: 1px;
          margin-left: 16px;
          border-radius: 48px;
          border: 1px solid #b4bdc3 !important;
          font-size: 12px;
          font-weight: 800;
          line-height: 11px;
          font-family: Mont;
          text-decoration: none;
          padding: 6px 8px;
          border: none;
          background-color: white;
          color: #89939a;
          cursor: pointer;
        }
      }
    }
  }

  &-buttons {
    & button {
      transition: color 0.3s ease;
      font-size: 12px;
      font-weight: 800;
      line-height: 11px;
      font-family: Mont;
      text-decoration: none;
      padding: 5px 10px;
      border: none;
      background-color: white;
      color: #89939a;
      cursor: pointer;
    }

    & :hover {
      color: black;
    }
  }
}

@media screen and (max-width: 639px) {
  .footer {
    &-content {
      gap: 32px;
      flex-direction: column;
      align-items: flex-start;
      background-color: white;
      justify-content: space-evenly;
    }

    &-container {
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;
    }

    &-buttons {
      flex-direction: column;
    }

    &-buttons button {
      padding-left: 0;
    }

    &-back-to-top {
      line-height: 15px;
      margin-left: 40%;
    }
    &-back-to-top {
      margin: 0;
      &-container {
        gap: 16px;
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }
  }
}
