@import '../../../utils/fonts.scss';
@import url('https://unpkg.com/open-props');
@import url('https://unpkg.com/open-props/normalize.min.css');
:-moz-any-link {
  --link: black;
}

.ReactModal__Content {
  position: relative !important;
  overflow: hidden !important;
  color: black;
  width: 60vh;
  height: 70vh;
  background: #d6cdc6 !important;
  border: 3px solid black !important;
  padding: 20px !important;
  border-radius: 10px !important;
  z-index: 1001;
  user-select: none;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 13px;
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 20px;
  font-weight: 800;
  line-height: 1;
  padding: 0;
}

.ReactModal__Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  font-family: Mont, sans-serif;
  display: flex;
  flex-direction: column;
}

.checkout__form {
  --link: black !important;
  outline-offset: 0 !important;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  font-family: Mont, sans-serif;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;

  & p {
    font-size: 13px;
    justify-self: center;
    width: 100%;
    text-align: center;
    margin-bottom: 10px;
  }

  &__field {
    width: 100%;
    border: 1px solid #b4bdc3;
    border-radius: 8px;
    height: 35px;
    color: #0f0f11;
    font-family: Mont, sans-serif;
    font-size: 14px !important;
    font-style: normal;
    font-weight: 700;
    line-height: 19px;
    margin-bottom: 10px;
    &__button {
      background-size: 200% auto;
      transition: 0.5s;
      background-image: linear-gradient(
        to right,
        #bf3c5d 0%,
        #c13d5d 25%,
        #fdaf69 100%
      );
      width: 32%;
      display: flex; /* Use flex display to center the button horizontally */
      justify-content: center;
      height: 22px;
    }
    &:hover {
      background-position: right center;
      border-color: #89939a;
    }

    &:focus {
      border-color: #0f0f11;
    }

    &:focus-visible {
      outline: none !important;
    }
  }

  &__checkbox {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 14px;
    // height: 50px;
    margin-left: 10px;
    justify-content: center;
    margin-bottom: 10px;
    &__input {
      margin-left: 10px;
    }
  }
}

@layer demo {
  .ReactModal__Content {
    aspect-ratio: var(--ratio-widescreen) !important;
    border: solid var(--size-2) transparent !important;
    border-radius: var(--radius-4) !important;
    background:
      linear-gradient(var(--surface-1), var(--surface-1)) padding-box,
      var(--gradient-1) border-box !important;
  }
}
